.header{
    display: none;
    padding: 0;
    margin: 0;
    height: 80px;
    z-index: 3;
    background:rgb(106, 185, 181);
    text-align: center;
    line-height: 80px;
    width: 100vw;
    position:fixed;
    overflow: hidden;
    transform-origin: 0;
}
.home{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 30px;
}
.bodycontainer{
    padding-top:80px ;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction:column;
}
.headload{
    position:relative;
    top: 75px;
    background-color: azure;
    width:100vw;
    height: 5px;
    display: none;
    background: linear-gradient(90deg,red,green,blue);
    overflow: hidden;
    animation: colorchange 4s ease-in-out infinite alternate;
    z-index: 3;
}
.fetchbutton{
   
    background-color: aquamarine;
    border-style: none;
    border-radius: 4px;
    width: 130px;
    height: 30px;
    box-shadow: 0px 0px 5px white;
    transition: 0.5s;
}
.fetchbutton:hover{
    box-shadow: 0px 0px 15px white;
}
.listitem{
    width: 90vw;
    height: 60px;
    margin: 2px;
    padding-left: 30px;
    background-color: rgba(119, 140, 160, 0.144); 
    transition: 0.3s ease-in;
    cursor: pointer;
}
.listitem:hover{
    background-color: rgba(152, 168, 182, 0.856);
}

.table{
    display: grid;
    padding: 15px;
    border-radius: 20px;
}
.maindiv{
    padding: 10px;
    margin: 10px;
    border: 2px solid brown;
    border-radius: 20px;
    transition: 0.5s;
    
}

.atCenter{
    box-shadow: 0px 0px 20px black;
    transform: scale(1.1);
}

#Detail{
   
    padding-top: 40px;
    padding-bottom:20px ;
    width: 80vw;
    display: flex;
    flex-direction: column;

}
.row{
    border: 1px solid blue;
    padding: 5px;
    display:grid;
    grid-template-columns: 50% 50%;
}
.intro{
   
    padding: 30px;

}
.backbutton{
    position:fixed;
    top:20px;
    z-index: 11;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    cursor: pointer;
    background-color: blueviolet;
    border-radius: 20px;
    transition: 1s;
    display: none;
}
.backbutton:hover{
    overflow: hidden;
    box-shadow: 0px 0px 10px black;
    transform: scale(1.2);
    
}



@keyframes colorchange {
    0%{
        width: 0px;
        transform: translateX(0px);
    }
    50%{
        width: 50vw;
        transform: translateX(50vw);
    }   
    100%{
        width: 0px;
        transform: translateX(98vw);
    }

}



