.toastbox{
  text-align: center;
  
}
.loading{
    width: 300px;
    height: 20px;
    background-color: red;
    animation: colorchangefortoast 9s linear infinite;
}
#toast{
    z-index: 10;
    position:absolute;
    width: 300px;
    left: 30px;
    top: 20px;
    height:50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(250, 32, 221);
    animation: fall 9s linear forwards;
    border-radius: 4px;
    transform-origin: 0;
    overflow: hidden;
}
@keyframes colorchangefortoast {
    0%{
        width: 0px;
        transform: translateX(-150px);
    }
    50%{
        width: 300px;
        transform: translateX(00px);
    }   
    100%{
        width: 0px;
        transform: translateX(150px);
    }

}

@keyframes fall {
    0%{
        transform:translateX(-300px);
    }
    20%{
        transform:translateX(0px);
    }
    40%{
        transform:rotate(0deg);
    }
    60%{
        transform:rotate(90deg);
    }
    70%{
        transform:  translateY(10px) rotate(80deg);
    }
    100%{
        transform: translateY(calc(100vh));
    }
    
}